import React from "react";
import s from "./Post.module.css";
const Post = (props) => {
  return (
    <div className={s.item}>
      <img src="https://resim.fullhdfilmizlesene.pw/mdsresim_orj/fullhd-avatar-izle.jpg?0.9550416874703249"></img>
      {props.message}

      <div>
        <span>Likes </span>
        {props.LikesCount}
      </div>
    </div>
  );
};

export default Post;
